import ETHWallet from "./ethWallet";
import Decimal from "decimal.js"
import abis from "@/utils/web3/contractABI/abis";
import * as constant from "@/utils/web3/constant";

import {onChainCall, offChainCall} from "./common";

let contracts = null
const tokenDecimals = constant.defaultDecimals

function loadContract() {
  if(contracts === null) {
    let web3Instance = ETHWallet.getWeb3Instance()
    contracts = {
      base: new web3Instance.eth.Contract(abis.erc20, constant.baseTokenAddr),
      pool: new web3Instance.eth.Contract(abis.pool, constant.poolAddr),
      unis: [],
    }

    for(let i = 0; i < constant.uniAddrs.length; i++) {
      contracts.unis.push(
        new web3Instance.eth.Contract(abis.erc20, constant.uniAddrs[i])
      )
    }
  }
}

async function commonApprove(contract, spender) {
  Decimal.set({ toExpPos: 256 })
  let maxApprove = new Decimal("0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff")

  return await onChainCall(
    contract,
    ETHWallet.getAccount(),
    "approve",
    [spender, maxApprove.toString()],
  )
}

async function enableDeposit(pid) {
  if(!ETHWallet.isConnected()) {
    return
  }
  
  loadContract()

  let uni = contracts.unis[pid]
  return commonApprove(uni, constant.poolAddr)
}

async function commonERC20Balance(contract, address, outDecimal) {
  let result = await offChainCall(contract, address, "balanceOf", [address])
  if(result !== null) {
    result = new Decimal(result).div(tokenDecimals)
  } else {
    result = new Decimal(0)
  }

  return outDecimal ? result:result.toDP(6, Decimal.ROUND_DOWN).toString()
}

async function deposit(pid, amount) {
  if(!ETHWallet.isConnected()) {
    return
  }

  loadContract()

  Decimal.set({ toExpPos: 256 })
  amount = new Decimal(amount).mul(tokenDecimals)

  return await onChainCall(
    contracts.pool,
    ETHWallet.getAccount(),
    "deposit",
    [pid, amount.toString()],
    "0",
  )
}

async function withdraw(pid, amount) {
  if(!ETHWallet.isConnected()) {
    return
  }

  loadContract()
  Decimal.set({ toExpPos: 256 })
  amount = amount.mul(tokenDecimals)

  return await onChainCall(
    contracts.pool,
    ETHWallet.getAccount(),
    "collect",
    [pid, amount.toString()],
  )
}

async function commonContractRead(contract, method, param = [], defVal = null) {
  if(!ETHWallet.isConnected()) {
    return defVal
  }

  loadContract()

  let owner = ETHWallet.getAccount()
  let result = await offChainCall(contract, owner, method, param)

  return result !== null ? result:defVal
}

async function poolInfo(pid) {
  if(!ETHWallet.isConnected()) {
    return false
  }

  loadContract()

  let owner = ETHWallet.getAccount()
  let pool = await commonContractRead(contracts.pool, "pools", [pid], 0)
  let user = await commonContractRead(contracts.pool, "users", [pid, owner], {stakeIn: 0,})
  let userToCollect = await commonContractRead(contracts.pool, "toBeCollectedOf", [pid, owner], 0)
  let poolToCollect = await commonContractRead(contracts.pool, "toBeCollectedOfPool", [pid], 0)
  let totalWeight = await commonContractRead(contracts.pool, "totalWeight", [], 0)

  let uni = contracts.unis[pid]
  let uniBalance = await commonERC20Balance(uni, owner, false)

  let orgStake = new Decimal(user.stakeIn).div(tokenDecimals)
  user.stakeIn = new Decimal(user.stakeIn).div(tokenDecimals).toString()

  poolToCollect = new Decimal(poolToCollect).div(tokenDecimals).toDP(4, Decimal.ROUND_FLOOR).toString()
  userToCollect = new Decimal(userToCollect).div(tokenDecimals).toDP(4, Decimal.ROUND_FLOOR).toString()

  return {
    pool,
    user,
    poolToCollect,
    userToCollect,
    totalWeight,
    orgStake,
    uniBalance,
  }
}


async function ethBalance(outDecimal) {
  let address = ETHWallet.getAccount()
  let result = await ETHWallet.getWeb3Instance().eth.getBalance(address)
    .catch(_=>{return null})
  if(result !== null) {
    result = new Decimal(result).div(1e18)
  } else {
    result = result = new Decimal(0)
  }

  return outDecimal ? result:result.toDP(6, Decimal.ROUND_DOWN).toString()
}

async function commonApproveCheck(token, to) {
  if(!ETHWallet.isConnected()) {
    return false
  }

  loadContract()

  let owner = ETHWallet.getAccount()
  let result = await offChainCall(token, owner, "allowance", [owner, to])

  if(result !== null) {
    let amount = new Decimal(result).div(tokenDecimals)
    return amount.gt(0)
  } else {
    return false
  }
}

async function isPoolEnable(pid) {
  if(!ETHWallet.isConnected()) {
    return false
  }

  loadContract()

  try {
    let uni = contracts.unis[pid]
    return await commonApproveCheck(uni, constant.poolAddr);
  } catch (e) {
    return false
  }
}

async function getBlockNum() {
  if(!ETHWallet.isConnected()) {
    return 0
  }

  loadContract()

  let web3Instance = ETHWallet.getWeb3Instance()
  return await web3Instance.eth.getBlockNumber().catch(_=>{return 0})
}

async function userTokenBalance() {
  if(!ETHWallet.isConnected()) {
    return 0
  }

  loadContract()
  let user = ETHWallet.getAccount()
  return commonERC20Balance(contracts.base, user, false)
}

async function userUniBalance(pid) {
  if(!ETHWallet.isConnected()) {
    return 0
  }

  loadContract()

  let uni = contracts.unis[pid]
  let user = ETHWallet.getAccount()
  return commonERC20Balance(uni, user, false)
}

async function poolCount() {
  if(!ETHWallet.isConnected()) {
    return false
  }

  loadContract()

  return commonContractRead(contracts.pool, "poolsCount", [])
}

async function mintReward() {
  if(!ETHWallet.isConnected()) {
    alert("no wallet (metamask) found")
    return false
  }

  loadContract()

  let user = ETHWallet.getAccount()

  return await onChainCall(contracts.pool, user, "mintToTeam", [])
}

export default {
  poolInfo,
  enableDeposit,
  deposit,
  withdraw,
  isPoolEnable,
  getBlockNum,
  userTokenBalance,
  userUniBalance,
  poolCount,
  mintReward,
}
